import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-cash-by-code-add-beneficiary02',
  templateUrl: 'cash-by-code-add-beneficiary02.html',
})
export class CashByCodeAddBeneficiary02Page {

  subscriptionDetails;
  constructor(public navCtrl: NavController,
              public navParams: NavParams) {
    let subscription=this.navParams.get("subscriptionDetails");
    this.subscriptionDetails=JSON.parse(subscription);
  }

  goToWelcome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  anotherICashRemit = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.push('CashByCode01Page');
  };

  ionViewDidLoad() {
  }

}



